import React, { Component } from 'react';
import freeWill from '../freeWill.jpg';
import range from '../range.jpg';
import oneWorld from '../oneWorld.jpg';
import normPeople from '../normPeople.jpg';

class Book extends Component{
    render(){
        return (
            <React.Fragment>
            <div id ={this.getId()} className ="book">
                <img className="bookCover img-responsive" src = {this.getImg()}></img>
                <div className="bookInfo">
                    <h1 className= "title">{this.props.book.name}</h1>
                    <h2 className="center">By {this.props.book.author}</h2>
                    <h2 className="center">Rating: {this.getRating()}</h2>
                </div>
                <br/><br/>
                <p id = {this.getSummaryId()} className="summary"> {this.props.book.summary}</p>
            </div>
            <br/><br/>
            <br/><br/>
            <br/><br/>
            <br/>
            </React.Fragment>
        );
    }
    getImg(){
        switch(this.props.book.id){
            case 1:
                return freeWill;
            case 2:
                return range;
            case 3:
                return oneWorld;
            case 4:
                return normPeople;
            default:
                return null;
        }
    }
    getId(){
        switch(this.props.book.id){
            case 1:
                return "Book1";
            case 2:
                return "Book2";
            case 3:
                return "Book3";
            case 4:
                return "Book4";
            default:
                return null;
        }
    }
    getSummaryId(){
        switch(this.props.book.id){
            case 1:
                return "Summary1";
            case 2:
                return "Summary2";
            case 3:
                return "Summary3";
            case 4:
                return "Summary4";
            default:
                return null;
        }
    }
    getRating(){
        switch(this.props.book.id){
            case 1:
                return "3.86";
            case 2:
                return "4.18";
            case 3:
                return "4.32";
            case 4:
                return "3.90";
            default:
                return null;
        }
    }
}

export default Book;